module.exports = "<div class=\"index-page page\"><div v-if=\"mode=='upload'\" class=panel><div class=panel-header><div class=left><div class=title>UPLOAD FILE</div></div><div class=right><div @click=clickAdd class=btn>ADD</div><div @click=clickSend v-show=files.length class=btn>SEND</div></div></div><div v-show=files.length class=panel-content><div v-for=\"file in files\" class=file><div class=title>{{file.name}} ({{filesizeStr(file.size)}})</div><div @click=clickRemove(file) class=btn>[X]</div></div><input id=inputFile type=file style=display:none multiple=multiple /></div></div><div v-if=\"mode=='uploading'||mode=='finished'\" class=panel><div class=panel-header><div class=left><div class=title>{{mode=='uploading'?'UPLOADING':'FINISHED'}}</div></div><div class=right><div @click=clickClose v-if=\"mode=='finished'\" class=btn>CLOSE</div></div></div><div class=panel-content>{{showStatus}}<div v-if=code class=download-link>URL : <a :href=downloadLink(this.code) target=_blank>{{downloadLink(this.code)}}</a></div><div v-if=!this.code&amp;&amp;!this.errorText class=progress><div :style=\"{width:(percent.toFixed() +'%') }\" class=progress-inner></div></div></div></div></div>";


